import React, { useContext } from "react";
import {
  FacebookIcon,
  FacebookShareButton,
  TwitterIcon,
  TwitterShareButton,
} from "react-share";
import { Context } from "../../utils/Reducer/AppContext";
export const PostButton: React.FC = () => {
  const contextData = useContext(Context);
  return (
    <div>
      {/* <FacebookIcon /> */}
      <FacebookShareButton
        quote={
          contextData?.state.gameStatus === "completed"
            ? `Hurray i guessed the today word in ${contextData.state.currentAttempt + 1
            } attempts`
            : "Hey let's see how you can do this"
        }
        hashtag="wordhash"
        url="https://mb2212.vanillanetworks.co.in"
      >
        <FacebookIcon round />
      </FacebookShareButton>
      <TwitterShareButton
        title={
          contextData?.state.gameStatus === "completed"
            ? `Hurray i guessed the today word in ${contextData.state.currentAttempt + 1
            } attempts`
            : "Hey let's see how you can do this"
        }
        url="https://mb2212.vanillanetworks.co.in"
        hashtags={["wordhash"]}
      >
        <TwitterIcon round />
      </TwitterShareButton>
    </div>
  );
};
