import { useCallback, useContext, useEffect } from "react";
import { actionsWords, Context } from "../../utils/Reducer/AppContext";
import "./KeyBoard.scss";
const KeyBoard: React.FC = () => {
  const keys = [
    ["q", "w", "e", "r", "t", "y", "u", "i", "o", "p"],
    ["a", "s", "d", "f", "g", "h", "j", "k", "l"],
    ["z", "x", "c", "v", "b", "n", "m"],
  ];
  const contextdata = useContext(Context);
  const onEnter = () => {
    contextdata?.dispatch({ type: actionsWords.checkTheWord, data: "" });
  };
  const handleKeyboard = useCallback(
    (event) => {
      if (event.key === "Enter") {
        onEnter();
      } else if (event.key === "Backspace") {
        contextdata?.dispatch({ type: actionsWords.onDelete, data: "" });
      } else {
        keys.map((row) => {
          return row.forEach((letter) => {
            if (letter === event.key) {
              return contextdata?.dispatch({
                type: actionsWords.onKeyPress,
                data: letter,
              });
            }
          });
        });
      }
    },
    [keys] // eslint-disable-line react-hooks/exhaustive-deps
  ); // eslint-disable-line react-hooks/exhaustive-deps
  useEffect(() => {
    document.addEventListener("keydown", handleKeyboard);
    return () => {
      document.removeEventListener("keydown", handleKeyboard);
    };
  }, [handleKeyboard]);

  return (
    <div className="keyboard-container">
      {keys.map((keyrow) => {
        return (
          <div key={Math.random()} className="keyboard-row">
            {keyrow.map((keyletter) => {
              return (
                <button
                  key={Math.random()}
                  className="keyboard-letter"
                  onClick={() => {
                    contextdata?.dispatch({
                      type: actionsWords.onKeyPress,
                      data: keyletter,
                    });
                  }}
                >
                  {keyletter}
                </button>
              );
            })}
          </div>
        );
      })}
      <div className="button-container ">
        <button
          className="check-button bg-red-400 rounded-lg text-black"
          onClick={() =>
            contextdata?.dispatch({ type: actionsWords.onDelete, data: "" })
          }
        >
          Delete
        </button>
        <button
          className="check-button bg-blue-400 rounded-lg text-black "
          onClick={onEnter}
        >
          Check
        </button>
      </div>
    </div>
  );
};

export default KeyBoard;
