import { useContext } from "react";
import GameCompleteScreen from "../gameCompleteScreen/GameCompleteScreen";
import { actionsWords, Context } from "../../utils/Reducer/AppContext";
import { CgCloseO } from "react-icons/cg";
import "./sharepost.scss";

import GameOverScreen from "../gameOverScreen/GameOverScreen";
import { PostButton } from "../../components/postButton/PostButton";
const SharePost: React.FC = () => {
  const contextData = useContext(Context);
  const isGameCompleted =
    contextData?.state.gameStatus === "completed" ? true : false;
  return (
    <div className="z-50 bg-white  w-full h-full">
      <div className="  w-full mt-12 pt-2 lg:mt-8 mb-6 mx-auto text-center">
        {isGameCompleted ? <GameCompleteScreen /> : <GameOverScreen />}
        <PostButton />
        {isGameCompleted ? (
          <div>
            {/* <button>Post</button> */}
            <button
              className=" text-3xl absolute top-3 right-3"
              onClick={() =>
                contextData?.dispatch({
                  type: actionsWords.onCancelPost,
                  data: "",
                })
              }
            >
              <CgCloseO />
            </button>
          </div>
        ) : (
          <div>
            <button
              className=" text-3xl absolute top-3 right-3"
              onClick={() =>
                contextData?.dispatch({
                  type: actionsWords.closemodal,
                  data: "",
                })
              }
            >
              <CgCloseO />
            </button>
          </div>
        )}
      </div>
    </div>
  );
};

export default SharePost;
