import { useContext } from "react";
import SharePost from "../../ui/SharePost/SharePost";
import { Context } from "../../utils/Reducer/AppContext";
import "./result.scss";
const Result: React.FC = () => {
  const contextData = useContext(Context);

  return (
    <div
      className=" lg:w-1/2 overflow-hidden shadow-2xl lg:h-5/6 w-10/12 h-4/5 mx-auto lg:rounded-3xl rounded-xl 
    absolute top-1/2 left-1/2 transform -translate-x-1/2 -translate-y-1/2
    mt-2 lg:mt-2 flex flex-wrap sharepost-container bg-blue-400/25 z-50 "
    >
      {contextData?.state.gameStatus === "completed" ? (
        <SharePost />
      ) : undefined}
      {contextData?.state.gameStatus === "over" ? <SharePost /> : undefined}
    </div>
  );
};

export default Result;
