import "./App.css";
import { Route, Routes } from "react-router";
import { useCookies } from "react-cookie";
import Home from "./components/Home/Home";
import NotFoundPage from "./components/NotfoundPage/NotFoundPage";
import React, { useContext, useEffect } from "react";
import { actionsWords, Context, stateType } from "./utils/Reducer/AppContext";
import shuffleArray from "./utils/shuffleArray";
import GetTheWords from "./utils/GetTheWords";
import AdminLogin from "./admin/adminLogin/AdminLogin";
import AdminInner from "./admin/adminLogin/AdminInner";
import moment from "moment";
import axios from "axios";
import { Helmet } from "react-helmet";
// export const ServerPort = 'http://localhost:8000'
export const ServerPort = "https://mb2212.vanillanetworks.co.in"
const App: React.FC = () => {
  const contextData = useContext(Context);
  const [cookies, setCookie] = useCookies(["v1"]);
  // const userTimeZoneLocale = Intl.DateTimeFormat().resolvedOptions().locale;
  // const convertedTime = new Date().toLocaleString(userTimeZoneLocale, {
  //   timeZone: "Asia/Kolkata",
  // });
  const parsedDate = new Date();

  useEffect(() => {
    contextData?.dispatch({
      type: actionsWords.setTimer,
      data: "",
      date: parsedDate,
    });
  }, []); // eslint-disable-line react-hooks/exhaustive-deps


  useEffect(() => {
    const timeInterval = setInterval(() => {
      contextData?.dispatch({ type: actionsWords.startTimer, data: "" });
    }, 1000);
    return () => clearInterval(timeInterval);
  }, [contextData?.state.timer]); // eslint-disable-line react-hooks/exhaustive-deps

  useEffect(() => {
    setCookie("v1", contextData?.state, {
      path: "/",
    });
  }, [  // eslint-disable-line react-hooks/exhaustive-deps
    contextData?.state.gameStatus,
    contextData?.state.currentAttempt,
    contextData?.state.correctWord,
    contextData?.state.todayTenLetters,
  ]); // eslint-disable-line react-hooks/exhaustive-deps
  const getCookies = (state: stateType) => {
    contextData?.dispatch({
      type: actionsWords.getcookiess,
      data: "",
      cookieState: state,
    });
  };
  useEffect(() => {
    // getting state stored in cookies
    getCookies(cookies.v1);
    GetTheWords().then((words) => {
      contextData?.dispatch({
        type: actionsWords.getWordBank,
        data: "",
        set: words.wordSet,
      });
    });
    // getting the date and month
    const getTodaysWord = async (today: string) => {
      try {
        const data = await axios.post(ServerPort + "/admin/gettodayword", { today });
        contextData?.dispatch({
          type: actionsWords.getNewStateForTheDay,
          data: data.data.word,
          lettersArray: shuffleArray(data.data.word),
        });
      } catch (error) {
        console.log(error);
      }
    };
    if (contextData?.state.presentDate !== parsedDate.getDate()) {
      // getting the new word of the day logic goes here
      getTodaysWord(moment().format("MM/DD/YYYY"));
    }
  }, []); // eslint-disable-line react-hooks/exhaustive-deps

  return (
    <div className="text-gray-600 body-font h-full overflow-x-hidden">
      <Helmet>
        {/* for twitter card */}
        <meta name="twitter:card" content="summary_large_image" />
        <meta name="twitter:site" content="@Wordhash" />
        <meta name="twitter:creator" content="@vanillanetworks" />
        <meta name="twitter:title" content="WORDHASH " />
        <meta name="twitter:description" content="Somedescription" />
        <meta
          name="twitter:image"
          content={`${ServerPort}/${contextData?.state.clueCardUrl}`}
        />
        {/* /for facebook */}
        <meta property="og:title" content="WORDHASH" />
        <meta
          property="og:image"
          content={`${ServerPort}/${contextData?.state.clueCardUrl}`}
        />
      </Helmet>
      <Routes>
        <Route path="/" element={<Home />} />
        <Route
          path="/admin"
          element={
            contextData?.state.IsAdminLoggedIN ? <AdminInner /> : <AdminLogin />
          }
        />
        <Route path="*" element={<NotFoundPage />} />
      </Routes>
    </div>
  );
};

export default App;
