import { useContext } from "react";
import { Context } from "../../utils/Reducer/AppContext";
import Timer from "../timer/Timer";
import confettiImage from "../../images/confetti.png";
import "./gamecompletescreen.scss";
const GameCompleteScreen: React.FC = () => {
  const contextData = useContext(Context);

  return (
    <>
      <h1 className=" font-extrabold text-xl lg:text-4xl lg:mb-2">WORDHASH</h1>
      <img src={confettiImage} alt="" className=" mx-auto" />
      <h2 className="font-normal lg:text-2xl text-xl">Congratulations</h2>
      <div className=" flex flex-row gap-1 justify-center">
        {contextData?.state.word.split("").map((letter) => {
          return (
            <span className=" uppercase bg-[#039df7] text-white font-extrabold p-3">
              {letter}
            </span>
          );
        })}
      </div>
      <div className="mt-2 font-semibold lg:text-lg">You Scored</div>
      <p className=" text-xl lg:text-2xl lg:mt-3">{`${
        contextData?.state.board.length! - contextData?.state.currentAttempt!
      }/${contextData?.state.board.length}`}</p>
      <div>
        <p className=" text-base lg:text-lg ">Next word will be in :</p>
        <Timer />
      </div>
    </>
  );
};

export default GameCompleteScreen;
